import { myMap } from "./index";
//utils functions
const baseUrl =
  "https://www.data.brisbane.qld.gov.au/data/api/3/action/datastore_search";

const getData = async (limit = null, search = null) => {
  try {
    var payload = {
      resource_id: "3c972b8e-9340-4b6d-8c7b-2ed988aa3343", // the resource id
      limit: limit, // get 5 results
      q: search // query for 'jones'
    };
    const response = await fetch(baseUrl, {
      body: JSON.stringify(payload), // must match 'Content-Type' header
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      mode: "cors", // no-cors, cors, *same-origin
      headers: {
        "content-type": "application/json"
      }
    });

    const data = await response.json();
    return data;
  } catch (err) {
    console.log(`Error: ${err}`);
    return null;
  }
};

//keep a record of active Item
const activeItems = new Map();

const buildList = (container, records) => {
  records.forEach((element) => {
    //console.log(element.Item_title);
    var item = document.createElement("button");
    item.innerHTML = `${element.Item_title} - ${element.Artist}`;
    item.setAttribute("id", `item-${element._id}`);
    item.setAttribute(
      "value",
      JSON.stringify({
        Latitude: element.Latitude,
        Longitude: element.Longitude,
        Description: element.Description
      })
    );
    //handle css
    item.classList.add("list-group-item");
    item.classList.add("list-group-item-action");
    item.classList.add("listItem");
    //handle event listener
    item.addEventListener("click", (e) => {
      let item = e.target;
      if (activeItems.has(item.id)) {
        console.log("remove");
        document.getElementById(item.id).classList.remove("active");
        /*
        let index = activeItems.indexOf(item.id);
        console.log(index);
        activeItems.splice(index,1);
        */
        //map related actions
        onItemClick(item, "remove");
        activeItems.delete(item.id);
      } else {
        console.log("add");
        document.getElementById(item.id).classList.add("active");
        //map related actions
        var marker = onItemClick(item, "add");
        if (marker) {
          activeItems.set(item.id, marker);
        } else {
          console.log("ERROR: Invalid Marker");
        }
      }
    });

    document.getElementById(container).appendChild(item);
  });
};

//generate map base on id, lat, long and zoomlevel
const generateMap = (id, lat, long, zoomLevel) => {
  const mymap = L.map(id).setView([lat, long], zoomLevel);
  //add tile
  const attribution =
    '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors';
  const tileUrl = "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png";
  const tiles = L.tileLayer(tileUrl, {
    attribution
  });
  tiles.addTo(mymap);
  return mymap;
};

const centerLeafletMapOnMarker = (map, marker) => {
  var latLngs = marker.getLatLng();
  //var markerBounds = L.latLngBounds(latLngs);
  map.setView([latLngs.lat, latLngs.lng], 15);
};

const onItemClick = (item, mode) => {
  console.log(item);
  if (mode === "add") {
    const value = JSON.parse(item.value);
    const latitude = value.Latitude;
    const longitude = value.Longitude;
    var marker = new L.Marker([latitude, longitude], { draggable: false });
    myMap.addLayer(marker);
    marker
      .bindPopup(`<b>${item.innerHTML}</b><br />${value.Description}`)
      .openPopup();
    centerLeafletMapOnMarker(myMap, marker);
    return marker;
  } else {
    var marker = activeItems.get(item.id);
    myMap.removeLayer(marker);
    return null;
  }
};

const setLoader = (mode) => {
  let loader = document.getElementById("loader");
  if (mode === "show") {
    loader.style.display = "block";
  } else if (mode === "hide") {
    loader.style.display = "none";
  }
};

const clearList = (container) => {
  const itemContainer = document.getElementById(container);
  while (itemContainer.firstChild) {
    itemContainer.removeChild(itemContainer.lastChild);
  }
};

export { getData, buildList, generateMap, setLoader, clearList };
