import 'regenerator-runtime/runtime';
import "./styles.css";
import { getData, buildList, generateMap, setLoader, clearList } from "./utils";

var myMap;

//main entry
const mainEntry = async () => {
  const searchButton = document.getElementById("searchButton");

  //acess API and get data
  var response = await getData(200);

  //build list
  if (response && response.result) {
    var records = response.result.records;
    //console.log(records);
    buildList("itemList", records);
    setLoader("hide");
  } else {
    console.log("Error: Invalid Response Format");
  }

  //search bar event listener
  searchButton.addEventListener("click", async (e) => {
    var input = document.getElementById("searchInput");
    if (typeof input.value === "string") {
      console.log(`new search: ${input.value}`);
      setLoader("show");

      let newResponse = await getData(200, input.value);
      //build list
      if (newResponse && newResponse.result) {
        let records = newResponse.result.records;
        clearList("itemList");
        //console.log(records);
        buildList("itemList", records);
        setLoader("hide");
      } else {
        console.log("Error: Invalid Response Format");
      }
    }
  });

  //generate map
  const lat = records[0].Latitude;
  const long = records[0].Longitude;
  myMap = generateMap("map", lat, long, 15);
  //add scale
  L.control.scale().addTo(myMap);
};

mainEntry();

//export map to be global
export { myMap };
